@font-face {
    font-family: "Pixelated MS Sans Serif";
    src: url(../fonts/ms_sans_serif.woff2) format("woff");
    src: url(../fonts/ms_sans_serif.woff2) format("woff2");
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: "Pixelated MS Sans Serif";
    src: url(../fonts/ms_sans_serif_bold.woff2) format("woff");
    src: url(../fonts/ms_sans_serif_bold.woff2) format("woff2");
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: "Pixelout";
    src: url(../fonts/Pixelout.ttf) format("truetype");
    font-weight: 700;
    font-style: normal;
}

/* 8px */
@font-face {
    font-family: MSSerif;
    src: url(../fonts/MSSansSerif.ttf);
}

@font-face {
    font-family: Millennium;
    src: url(../fonts/Millennium.ttf);
}

@font-face {
    font-family: MillenniumBold;
    src: url(../fonts/Millennium-Bold.ttf);
}

@font-face {
    font-family: Terminal;
    src: url(../fonts/Terminal.ttf);
}

body {
    font-family: Arial;
    font-size: 12px;
    color: #222;
}

.title-bar,
.window,
button,
input,
label,
option,
select,
textarea,
ul.tree-view {
    font-family: "Pixelated MS Sans Serif", Arial;
    -webkit-font-smoothing: antialiased;
    font-size: 11px;
}

h1 {
    font-size: 5rem;
}

h2 {
    font-size: 2.5rem;
}

h3 {
    font-size: 2rem;
}

h4 {
    font-size: 1.5rem;
}

u {
    text-decoration: none;
    border-bottom: 0.5px solid #222;
}

button {
    box-sizing: border-box;
    border: none;
    border-radius: 0;
    min-width: 75px;
    min-height: 23px;
    padding: 0 12px;
}

.vertical-bar,
button {
    background: silver;
    box-shadow: inset -1px -1px #0a0a0a, inset 1px 1px #fff, inset -2px -2px grey, inset 2px 2px #dfdfdf;
}

.vertical-bar {
    width: 4px;
    height: 20px;
}

button:not(:disabled):active {
    box-shadow: inset -1px -1px #fff, inset 1px 1px #0a0a0a, inset -2px -2px #dfdfdf, inset 2px 2px grey;
    padding: 2px 11px 0 13px;
}

@media (not(hover)) {
    button:not(:disabled):hover {
        box-shadow: inset -1px -1px #fff, inset 1px 1px #0a0a0a, inset -2px -2px #dfdfdf, inset 2px 2px grey;
    }
}

button:focus {
    outline: 1px dotted #000;
    outline-offset: -4px;
}

button::-moz-focus-inner {
    border: 0;
}

:disabled,
:disabled + label {
    color: grey;
    text-shadow: 1px 1px 0 #fff;
}

.window {
    box-shadow: inset -2px -5px #262626, inset 1px 1px #dfdfdf, inset -2px -2px grey, inset 2px 2px #fff;
    background: #cac3bd;
    padding: 3px;
}

.title-bar {
    background: linear-gradient(90deg, #698858, #97bd82);
    padding: 3px 2px 3px 3px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.title-bar.inactive {
    background: linear-gradient(90deg, grey, #b5b5b5);
}

.title-bar-text {
    font-weight: 700;
    color: #fff;
    letter-spacing: 0;
    margin-right: 24px;
}

.title-bar-controls {
    display: flex;
}

.title-bar-controls button {
    padding: 0;
    display: block;
    min-width: 16px;
    min-height: 14px;
}

.title-bar-controls button:active {
    padding: 0;
}

.title-bar-controls button:focus {
    outline: none;
}

.title-bar-controls button[aria-label=Minimize] {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='6' height='2' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23000' d='M0 0h6v2H0z'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: bottom 3px left 4px;
}

.title-bar-controls button[aria-label=Maximize] {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='9' height='9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9 0H0v9h9V0zM8 2H1v6h7V2z' fill='%23000'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: top 2px left 3px;
}

.title-bar-controls button[aria-label=Restore] {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='8' height='9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23000' d='M2 0h6v2H2zM7 2h1v4H7zM2 2h1v1H2zM6 5h1v1H6zM0 3h6v2H0zM5 5h1v4H5zM0 5h1v4H0zM1 8h4v1H1z'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: top 2px left 3px;
}

.title-bar-controls button[aria-label=Help] {
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='6' height='9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill='%23000' d='M0 1h2v2H0zM1 0h4v1H1zM4 1h2v2H4zM3 3h2v1H3zM2 4h2v2H2zM2 7h2v2H2z'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: top 2px left 5px;
}

.title-bar-controls button[aria-label=Close] {
    margin-left: 2px;
    background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg width='8' height='7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M0 0h2v1h1v1h2V1h1V0h2v1H7v1H6v1H5v1h1v1h1v1h1v1H6V6H5V5H3v1H2v1H0V6h1V5h1V4h1V3H2V2H1V1H0V0z' fill='%23000'/%3E%3C/svg%3E");
    background-repeat: no-repeat;
    background-position: top 3px left 4px;
}

.window-body {
    margin: 8px;
}

.window-site-page {
    height: 100%;
    display: flex;
    justify-content: center; /* ✅ center horizontally */
    align-items: center;     /* ✅ center vertically */
    position: relative; 
    display: flex;
    /* left: 0px; */
    /* right: 0px; */
    /* position: absolute; */
    /* bottom: 0px;
    top: 0px;
    overflow: scroll; */
    /* transform: translate3d(0, 0, 0); */
    /* overflow: hidden; */
}

.site-page-content {
    height: 100%;
    display: flex;
    flex-direction: column;
    width: 100%;

    padding-top: 64px;
    overflow-y: scroll;
    background: rgba(251, 243, 232, 1);
    background-image:
    linear-gradient(rgba(248, 236, 223, 0.75) 3px, transparent 3px),
    linear-gradient(90deg, rgba(248, 236, 223, 0.75) 3px, transparent 3px);
    background-size: 40px 40px;
}

.page-background {
    margin: 25px;
    background: rgba(255, 255, 255, 0.681);
}

.scrollArea::-webkit-scrollbar {
  display: none;
}

fieldset {
    border: none;
    box-shadow: inset -1px -1px #fff, inset 1px 1px #0a0a0a, inset -2px -2px grey, inset 2px 2px #dfdfdf;
    padding: 10px;
    padding-block-start: 8px;
    margin: 0;
}

legend {
    background: silver;
}

.field-row {
    display: flex;
    align-items: center;
}

[class^=field-row] + [class^=field-row] {
    margin-top: 6px;
}

.field-row > * + * {
    margin-left: 6px;
}

.field-row-stacked {
    display: flex;
    flex-direction: column;
}

.field-row-stacked * + * {
    margin-top: 6px;
}

label {
    display: inline-flex;
    align-items: center;
}

input[type=checkbox],
input[type=radio] {
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    margin: 0;
    background: 0;
    position: fixed;
    opacity: 0;
    border: none;
}

input[type=checkbox] + label,
input[type=radio] + label {
    line-height: 13px;
}

input[type=radio] + label {
    position: relative;
    margin-left: 18px;
}

