input[type=checkbox] + label {
    position: relative;
    margin-left: 19px;
}

input[type=checkbox] + label:before {
    content: "";
    position: absolute;
    left: -19px;
    display: inline-block;
    width: 13px;
    height: 13px;
    background: #fff;
    box-shadow: inset -1px -1px #fff, inset 1px 1px grey, inset -2px -2px #dfdfdf, inset 2px 2px #0a0a0a;
    margin-right: 6px;
}

input[type=checkbox]:active + label:before {
    background: silver;
}

input[type=checkbox]:checked + label:after {
    content: "";
    display: block;
    width: 7px;
    height: 7px;
    position: absolute;
    top: 3px;
    left: -16px;
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg width='7' height='7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7 0H6v1H5v1H4v1H3v1H2V3H1V2H0v3h1v1h1v1h1V6h1V5h1V4h1V3h1V0z' fill='%23000'/%3E%3C/svg%3E");
}

input[type=checkbox][disabled] + label:before {
    background: silver;
}

input[type=checkbox][disabled]:checked + label:after {
    background: url("data:image/svg+xml;charset=utf-8,%3Csvg width='7' height='7' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M7 0H6v1H5v1H4v1H3v1H2V3H1V2H0v3h1v1h1v1h1V6h1V5h1V4h1V3h1V0z' fill='gray'/%3E%3C/svg%3E");
}

input[type=email],
input[type=password],
input[type=text] {
    border: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
}

input[type=email],
input[type=password],
input[type=text],
select {
    padding: 3px 4px;
    box-shadow: inset -1px -1px #fff, inset 1px 1px grey, inset -2px -2px #dfdfdf, inset 2px 2px #0a0a0a;
    background-color: #fff;
    box-sizing: border-box;
}

select,
textarea {
    border: none;
}

textarea {
    padding: 3px 4px;
    box-shadow: inset -1px -1px #fff, inset 1px 1px grey, inset -2px -2px #dfdfdf, inset 2px 2px #0a0a0a;
    background-color: #fff;
    box-sizing: border-box;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border-radius: 0;
}

input[type=email],
input[type=password],
input[type=text],
select {
    height: 21px;
}

input[type=email],
input[type=password],
input[type=text] {
    line-height: 2;
}

